<template>
  <div id="order-submited">
    <order-nav></order-nav>
    <h3>已提交工单</h3>
    <!-- 工单查询部分 -->
    <div class="order_query">
      <div class="left">
        <div class="select">
          <span>结单部门</span>
          <el-select
            v-model="queryInfo.form.checkedDpm"
            placeholder="请选择"
            @change="departmentChange"
            clearable
          >
            <el-option
              v-for="(item,index) in queryInfo.departmentList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </div>
        <div class="select">
          <span>接单人</span>
          <el-select v-model="queryInfo.form.checkedStaff" placeholder="请选择" clearable>
            <el-option
              v-for="(item,index) in queryInfo.staff"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </div>
        <div class="select">
          <span>工单类型</span>
          <el-select v-model="queryInfo.form.checkedType" placeholder="请选择" clearable>
            <el-option
              v-for="(item,index) in queryInfo.type"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </div>
        <div class="select">
          <span>结单时间</span>
          <el-date-picker
            v-model="queryInfo.form.checkedDate"
            type="date"
            placeholder="选择日期"
            format="yyyy/MM/dd"
            value-format="yyyy/MM/dd"
          ></el-date-picker>
        </div>
      </div>
      <div class="right">
        <el-button type="primary" @click="queryOrder" size="small">查询</el-button>
      </div>
    </div>
    <!-- 已提交工单汇总 -->
    <div class="order_table">
      <!-- 按钮部分 -->
      <div class="operate" @click="operateClick">
        <!-- <el-button type="primary" size="mini">置顶</el-button> -->
        <el-button type="primary" size="mini">编辑</el-button>
        <el-button type="danger" size="mini">删除</el-button>
        <el-button type="primary" size="mini" @click="orderSubmitClick">工单提交申请</el-button>
      </div>
      <div class="table_container">
        <el-table
          :data="orderGatherList"
          border
          style="width: 100%"
          max-height="500"
          size="small"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection"></el-table-column>
          <el-table-column type="index" label="序号" width="60"></el-table-column>
          <el-table-column prop="receive_person" label="接单人"></el-table-column>
          <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
          <el-table-column prop="out_time" label="结单时间"></el-table-column>
          <el-table-column prop="need" label="内容需求"></el-table-column>
          <el-table-column prop="next_info" label="跟进信息"></el-table-column>
          <el-table-column prop="job_type" label="工单类型"></el-table-column>
          <el-table-column prop="status" label="状态"></el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="{row}">
              <div v-if="row.status == '结单中'">
                <el-button type="primary" size="mini" @click="jiedanConfirm(row.id)">结单确认</el-button>
                <el-button type="danger" size="mini" @click="jiedanReject(row.id)">驳回</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!-- 编辑对话框 -->
    <el-dialog
      title="提示"
      :visible.sync="editOrderDialog.dialogVisible"
      width="30%"
      center
      @close="editOrderDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <!-- 表单部分 -->
      <el-form
        :model="editOrderDialog.editForm"
        :rules="formRules"
        ref="editOrderRef"
        label-width="100px"
      >
        <el-form-item label="接单人">
          <el-input readonly v-model="editOrderDialog.editForm.receivePerson"></el-input>
        </el-form-item>
        <el-form-item label="发单时间">
          <el-input readonly v-model="editOrderDialog.editForm.issuingTime"></el-input>
        </el-form-item>
        <el-form-item label="截单时间" prop="cutOffTime">
          <el-date-picker
            v-model="editOrderDialog.editForm.cutOffTime"
            type="datetime"
            placeholder="选择日期"
            format="yyyy/MM/dd HH:mm"
            value-format="yyyy/MM/dd HH:mm"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="内容需求" prop="contentInfo">
          <el-input type="textarea" v-model="editOrderDialog.editForm.contentInfo"></el-input>
        </el-form-item>
        <el-form-item label="跟进信息" prop="followInfo">
          <el-input type="textarea" v-model="editOrderDialog.editForm.followInfo"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editOrderConfirm">确 认</el-button>
      </span>
    </el-dialog>

    <!-- 工单提交申请对话框 -->
    <el-dialog
      title="工单提交申请"
      :visible.sync="orderSubmitDialog.dialogVisible"
      width="30%"
      center
      @close="orderSubmitDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <!-- 表单内容 -->
      <el-form
        :model="orderSubmitDialog.form"
        :rules="formRules"
        ref="orderSubmitRef"
        label-width="100px"
      >
        <el-form-item label="接单部门" prop="department">
          <el-select
            v-model="orderSubmitDialog.form.department"
            placeholder="请选择"
            @change="departmentChange"
          >
            <el-option
              v-for="(item, index) in queryInfo.departmentList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="接单人" prop="staff">
          <el-select v-model="orderSubmitDialog.form.staff" placeholder="请选择">
            <el-option
              v-for="(item, index) in queryInfo.staff"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="工单类型" prop="type">
          <el-select v-model="orderSubmitDialog.form.type" placeholder="请选择">
            <el-option
              v-for="(item, index) in queryInfo.type"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="截单时间" prop="cutOffTime">
          <el-date-picker
            v-model="orderSubmitDialog.form.cutOffTime"
            type="datetime"
            placeholder="选择日期"
            format="yyyy/MM/dd HH:mm"
            value-format="yyyy/MM/dd HH:mm"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="内容需求" prop="contentInfo">
          <el-input v-model="orderSubmitDialog.form.contentInfo" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="orderSubmitConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import orderNav from "@/common/order_nav";
export default {
  name: "",
  props: {},
  components: {
    orderNav
  },
  data() {
    return {
      // 查询相关数据
      queryInfo: {
        form: {
          // 选中的接单部门
          checkedDpm: "",
          // 选中的接单人
          checkedStaff: "",
          // 选中的工单类型
          checkedType: "",
          // 选中的截单时间
          checkedDate: ""
        },
        // 部门列表
        departmentList: [],
        // 接单人列表
        staff: [],
        // 工单类型列表
        type: ["即时", "远期"]
      },
      // 已提交工单列表
      orderGatherList: [],
      // 选中的多选框列表(是否被选中)
      checkedList: [],
      // 点击删除获取选中的多选框列表（下标）
      indexList: [],
      // 编辑对话框所需数据
      editOrderDialog: {
        // 是否显示
        dialogVisible: false,
        // 表单内容
        editForm: {
          // 截单时间
          cutOffTime: "",
          // 内容需求
          contentInfo: "",
          // 跟进信息
          followInfo: "",
          // 员工id
          jobId: "",
          // 接单人
          receivePerson: "",
          // 发单时间
          issuingTime: ""
        }
      },
      // 工单提交申请对话框
      orderSubmitDialog: {
        dialogVisible: false,
        form: {
          type: "",
          contentInfo: "",
          cutOffTime: "",
          department: "",
          staff: ""
        }
      },
      // 表单验证
      formRules: {
        contentInfo: [
          { required: true, message: "请填写内容需求", trigger: "blur" }
        ],
        followInfo: [
          { required: true, message: "请填写跟进信息", trigger: "blur" }
        ],
        cutOffTime: [
          {
            type: "string",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        department: [
          {
            type: "string",
            required: true,
            message: "请选择接单部门",
            trigger: "change"
          }
        ],
        staff: [
          {
            type: "string",
            required: true,
            message: "请选择接单人",
            trigger: "change"
          }
        ],
        type: [{ required: true, message: "请选择工单类型", trigger: "change" }]
      },
      // 多选
      selection: [],
      selectNum: 0
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取页面数据
    getOrderList() {
      const query = this.queryInfo.form;
      this.$api.queryInfo(query).then(res => {
        console.log(res);
        this.orderGatherList = res.data.data;
      });
    },
    // 获取部门下拉框数据
    getOrderSelectData() {
      this.$api.getOrderSelectData().then(res => {
        console.log(res);
        this.queryInfo.departmentList = res.data.data;
      });
    },
    // 操作按钮被点击，事件代理
    operateClick(ev) {
      if (ev.target.innerText == "工单提交申请") return;
      if (this.selectNum == 0) return this.$message.error("请选择一个工单");
      // 判断事件源
      // 编辑按钮
      if (ev.target.innerText == "编辑") {
        // 判断是否多选
        if (this.selectNum > 1)
          return this.$message.error("只可以编辑一个工单");
        const form = this.editOrderDialog.editForm;
        form.receivePerson = this.selection[0].initiate_person;
        form.issuingTime = this.selection[0].initiate_time;
        form.cutOffTime = this.selection[0].out_time;
        form.contentInfo = this.selection[0].need;
        form.followInfo = this.selection[0].next_info;
        form.jobId = this.selection[0].id;
        this.editOrderDialog.dialogVisible = true;
      }
      // 删除按钮
      else if (ev.target.innerText == "删除") {
        this.$confirm("此操作将永久删除工单, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            let job_id = [];
            for (let item of this.selection) {
              job_id.push(item.id);
            }
            console.log(job_id);

            this.$api.removeOrder(job_id).then(res => {
              console.log(res);
              this.getOrderList();
              this.$message.success("删除工单成功");
            });
          })
          .catch(() => {});
      }
      // 置顶按钮
      else if (ev.target.innerText == "置顶") {
        for (let i in this.indexList) {
          let top = this.orderGatherList[this.indexList[i]];
          this.orderGatherList.splice(this.indexList[i], 1);
          this.orderGatherList.unshift(top);
        }

        // 响应式更新数组
        for (let i in this.checkedList) {
          this.$set(this.checkedList, i, false);
        }
      }
    },
    // 表格多选
    handleSelectionChange(selection) {
      this.selection = selection;
      this.selectNum = selection.length;
    },
    // 工单提交申请，弹出对话框
    orderSubmitClick() {
      this.orderSubmitDialog.dialogVisible = true;
    },
    // 添加工单提交申请
    orderSubmitConfirm() {
      this.$refs.orderSubmitRef.validate(state => {
        if (!state) return;
        console.log(this.orderSubmitDialog.form);
        let formData = new FormData();
        formData.append(
          "receive_department",
          this.orderSubmitDialog.form.department
        );
        formData.append("receive_person", this.orderSubmitDialog.form.staff);
        formData.append("job_type", this.orderSubmitDialog.form.type);
        formData.append("out_time", this.orderSubmitDialog.form.cutOffTime);
        formData.append("need", this.orderSubmitDialog.form.contentInfo);
        formData.append("user_name", this.$store.state.userName);
        this.$api.orderSubmit(formData).then(res => {
          if (res) {
            console.log(res);
            this.getOrderList();
            this.getOrderSelectData();
            this.$message.success("添加工单成功");
            this.orderSubmitDialog.dialogVisible = false;
          }
        });
      });
    },
    // 编辑工单提交
    editOrderConfirm() {
      this.$refs.editOrderRef.validate(state => {
        if (!state) return;
        let formData = new FormData();
        formData.append("job_id", this.editOrderDialog.editForm.jobId);
        formData.append("out_time", this.editOrderDialog.editForm.cutOffTime);
        formData.append("need", this.editOrderDialog.editForm.contentInfo);
        formData.append("next_info", this.editOrderDialog.editForm.followInfo);
        formData.append("user_name", this.$store.state.userName);
        this.$api.editOrder(formData).then(res => {
          console.log(res);
          this.getOrderList();
          this.$message.success("编辑工单成功");
          this.editOrderDialog.dialogVisible = false;
        });
      });
    },
    // 部门选择触发 提供相应员工
    departmentChange(val) {
      this.$api.getReceivePerson(val).then(res => {
        console.log(res);
        this.queryInfo.staff = res.data.data;
      });
    },
    // 查询
    queryOrder() {
      this.getOrderList();
    },
    // 结单确认
    jiedanConfirm(id) {
      console.log(id);
      this.$confirm("确认要结单吗？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.jiedanConfirm(id).then(res => {
            console.log(res);
            this.$message.success("结单成功");
            this.getOrderList();
          });
        })
        .catch(() => {});
    },
    // 驳回
    jiedanReject(id) {
      this.$confirm("确认要驳回吗？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.jiedanReject(id).then(res => {
            console.log(res);
            this.$message.success("驳回成功");
            this.getOrderList();
          });
        })
        .catch(() => {});
    },
    // 对话框关闭事件
    orderSubmitDialogClose() {
      this.$refs.orderSubmitRef.resetFields();
    },
    // 对话框关闭事件
    editOrderDialogClose() {
      this.$refs.editOrderRef.resetFields();
    }
  },
  created() {
    this.getOrderList();
    this.getOrderSelectData();
  },
  mounted() {}
};
</script>
<style>
#order-submited .order_query .form_query .el-form-item__label {
  color: #fff;
}
</style>
<style scoped>
#order-submited {
  height: 92vh;
  background: rgba(255, 255, 255, 0.7);
}
/* @import url("../../../../static/css/thead_fixed_table.css"); */
.order_query {
  display: flex;
  padding-top: 10px;
  align-items: center;
  justify-content: center;
}
.order_query .left {
  margin-right: 24px;
  display: flex;
  align-items: center;
}
.order_query .left .select {
  margin-right: 20px;
}
.form_query {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.order_table {
  margin-top: 24px;
  position: relative;
}
.table_container {
  position: relative;
  width: 1380px;
  margin: 0 auto;
  padding-bottom: 120px;
}
table {
  padding-top: 25px;
}
thead {
  display: table-header-group;
}
tbody {
  display: table-row-group;
}
tbody td:last-child {
  width: 230px;
}
.operate {
  margin-bottom: 10px;
  text-align: center;
}
h3 {
  text-align: center;
}
</style>


